import React, { createContext, useState, useContext } from 'react';

const DataContext = createContext();

export const DataProvider = ({ children }) => {
  const [users, setUsers] = useState([
    { name: 'Juan Prez', role: 'tutor', subjects: ['Fsica I', 'Clculo II'], year: 2, rating: 4 },
    { name: 'Ana Lpez', role: 'tutor', subjects: ['Mate discreta', 'Teora de probabilidades'], year: 3, rating: 5 },
    { name: 'Marta Hernndez', role: 'tutor', subjects: ['Historia Universal'], year: 2, rating: 3 },
    { name: 'Luis Gmez', role: 'tutor', subjects: ['Clculo I', 'Pensamiento cuantitativo'], year: 4, rating: 2 },
    { name: 'Mara Daz', role: 'tutor', subjects: ['Clculo II', 'Teora de probabilidades'], year: 3, rating: 5 },
    { name: 'Ricardo Snchez', role: 'tutor', subjects: ['Matemticas Bsicas'], year: 1, rating: 4 },
    { name: 'Carlos Garca', role: 'estudiante', subjects: [], year: 1, rating: 0 },
    { name: 'Sofa Martnez', role: 'estudiante', subjects: [], year: 4, rating: 0 },
    { name: 'Luca Gmez', role: 'estudiante', subjects: [], year: 2, rating: 0 },
    { name: 'Pedro Fernndez', role: 'estudiante', subjects: [], year: 3, rating: 0 },
    { name: 'Jorge Ruiz', role: 'estudiante', subjects: [], year: 2, rating: 0 },
    { name: 'Elena Torres', role: 'estudiante', subjects: [], year: 1, rating: 0 },
  ]);

  const toggleUserRole = (index) => {
    setUsers((prevUsers) => {
      const updatedUsers = [...prevUsers];
      updatedUsers[index].role = updatedUsers[index].role === 'tutor' ? 'estudiante' : 'tutor';
      return updatedUsers;
    });
  };

  return (
    <DataContext.Provider value={{ users, toggleUserRole }}>
      {children}
    </DataContext.Provider>
  );
};

export const useData = () => useContext(DataContext);
